import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'sliceDemo'
})
export class SliceDemoPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    var fruits = ["Banana", "Orange", "Lemon", "Apple", "Mango"];
var citrus = fruits.slice(1, 3);
    return citrus;
  }

}
