import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'demo'
})
export class DemoPipe implements PipeTransform {

  transform(value: String, args?: any): any {
    var x=0,i;
    for(i=0; i<=9; i++){
      x += i;
    }
    // return value + value.length.toString();
    return  value + x.toString();
  }

}
