import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'arrayDemo'
})
export class ArrayDemoPipe implements PipeTransform {

  transform(value: String, args?: any): any {
    var fruits = ["Banana", "Orange", "Apple", "Mango"];
    var energy = fruits.join();
    return energy;
  }

}
