import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'addDemo'
})
export class AddDemoPipe implements PipeTransform {

  transform(value: string, args?: any): any {
    var mainnum=[12, 13, 14, 15];
    var num1 = mainnum[0];
    var num2 = mainnum[1];
    var num3 = mainnum[2];
    var num4 = mainnum[3];
    var add = num1+num2+num3+num4;
    return add;
  }

}
